"use client"

import { useEffect, useRef } from "react"
import dynamic from "next/dynamic"
import { buildGraph } from "../lib/graphTransform"
import { ForensicResponse } from "../types/forensic"

// Dynamically import Sigma with SSR disabled
const Sigma = dynamic(() => import("sigma"), {
  ssr: false,
})

export default function GraphView({ data }: { data: ForensicResponse }) {
  const containerRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    if (!containerRef.current) return

    const graph = buildGraph(data)

    // Import sigma inside effect to guarantee browser-only execution
    import("sigma").then((SigmaModule) => {
      const SigmaRenderer = SigmaModule.default
      const renderer = new SigmaRenderer(graph, containerRef.current!)

      return () => renderer.kill()
    })
  }, [data])

  return (
    <div
      ref={containerRef}
      className="h-[600px] w-full border rounded-lg bg-white"
    />
  )
}
