"use client"

import { useState } from "react"
import UploadPanel from "../components/UploadPanel"
import GraphView from "../components/GraphView"
import { ForensicResponse } from "../types/forensic"

export default function Home() {
  const [data, setData] = useState<ForensicResponse | null>(null)

  return (
    <main className="p-8 space-y-6">
      <h1 className="text-3xl font-bold">
        Financial Forensics Engine
      </h1>

      <UploadPanel onData={setData} />

      {data && (
        <>
          <div className="grid grid-cols-3 gap-4">
            <div>Total Accounts: {data.summary.total_accounts_analyzed}</div>
            <div>Suspicious: {data.summary.suspicious_accounts_flagged}</div>
            <div>Rings: {data.summary.fraud_rings_detected}</div>
          </div>

          <GraphView data={data} />
        </>
      )}
    </main>
  )
}
